import Vue from 'vue'
import Router from 'vue-router'

const Main = (resolve) => {
  import('@/main/main').then((module) => {
    resolve(module)
  })
}

const Full = (resolve) => {
  import('@/main/full').then((module) => {
    resolve(module)
  })
}
// 登录
const Login = (resolve) => {
  import('components/login/login').then((module) => {
    resolve(module)
  })
}

// 首页
const Home = (resolve) => {
  import('components/home/home').then((module) => {
    resolve(module)
  })
}

const HomeSystem = (resolve) => {
  import('components/home/system/system').then((module) => {
    resolve(module)
  })
}

const HomeLog = (resolve) => {
  import('components/home/log/log').then((module) => {
    resolve(module)
  })
}
// 预约挂号
const Bespeak = (resolve) => {
  import('components/bespeak/bespeak').then((module) => {
    resolve(module)
  })
}

const BespeakManagement = (resolve) => {
  import('components/bespeak/bespeakManagement/bespeakManagement').then((module) => {
    resolve(module)
  })
}

const SchedulingManagement = (resolve) => {
  import('components/bespeak/schedulingManagement/schedulingManagement').then((module) => {
    resolve(module)
  })
}

// 介绍
const Intro = (resolve) => {
  import('components/intro/intro').then((module) => {
    resolve(module)
  })
}
const IntroHospital = (resolve) => {
  import('components/intro/hospital/hospital').then((module) => {
    resolve(module)
  })
}
const IntroDoctor = (resolve) => {
  import('components/intro/doctor/doctor').then((module) => {
    resolve(module)
  })
}

// 会员
const Members = (resolve) => {
  import('components/members/members').then((module) => {
    resolve(module)
  })
}

const MemberList = (resolve) => {
  import('components/members/memberList/memberList').then((module) => {
    resolve(module)
  })
}

const MemberScore = (resolve) => {
  import('components/members/memberScore/memberScore').then((module) => {
    resolve(module)
  })
}

// 权限
// const Power = (resolve) => {
//   import('components/power/power').then((module) => {
//     resolve(module)
//   })
// }
// const PowerRole = (resolve) => {
//   import('components/power/role/role').then((module) => {
//     resolve(module)
//   })
// }
// const PowerMember = (resolve) => {
//   import('components/power/member/member').then((module) => {
//     resolve(module)
//   })
// }
// const PowerConsult = (resolve) => {
//   import('components/power/consult/consult').then((module) => {
//     resolve(module)
//   })
// }
// const PowerHandle = (resolve) => {
//   import('components/power/handle/handle').then((module) => {
//     resolve(module)
//   })
