Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
      component: Full,
      children: [
        {
          path: 'login',
          component: Login
        }
      ]
    },
    {
      path: '/',
      redirect: '/home/system',
      component: Main,
      children: [
        {
          path: 'home',
          redirect: '/home/system',
          component: Home,
          children: [
            {
              path: 'system',
              component: HomeSystem
            },
            {
              path: 'log',
              component: HomeLog
            }
          ]
        },
        {
          path: 'bespeak',
          redirect: '/bespeak/bespeakManagement',
          component: Bespeak,
          children: [
            {
              path: 'bespeakManagement',
              component: BespeakManagement
            },
            {
              path: 'schedulingManagement',
              component: SchedulingManagement
            }
          ]
        },
        {
          path: 'intro',
          redirect: '/intro/hospital',
          component: Intro,
          children: [
            {
              path: 'hospital',
              component: IntroHospital
            },
            {
              path: 'doctor',
              component: IntroDoctor
            }
          ]
        },
        {
          path: 'members',
          redirect: '/members/memberList',
          component: Members,
          children: [
            {
              path: 'memberList',
              component: MemberList
            },
            {
              path: 'memberScore',
              component: MemberScore
            }
          ]
        }
        // {
        //   path: 'power',
        //   redirect: '/power/role',
        //   component: Power,
        //   children: [
        //     {
        //       path: 'role',
        //       component: PowerRole
        //     },
        //     {
        //       path: 'member',
        //       component: PowerMember
        //     },
        //     {
        //       path: 'consult',
        //       component: PowerConsult
        //     },
        //     {
        //       path: 'handle',
        //       component: PowerHandle
        //     }
        //   ]
        // }
      ]
    }
  ]
})



